            margin: 10px 10px 0 10px;
            &:hover {
                animation: pulse 1s infinite;
            } 
        }
        .deleteItem {
            color: $accent;
            height: 25px;
            width: 25px;
            &:hover {
                color: #d4655d;
            }
        }
        .resetItem {
