//STAR RATING
.star-rating {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    &__title {
        display: flex;
        width: 100%;
        font-weight: 400;
        color: #ffffff;
        font-size: 18px;
        margin-bottom: 20px;
        span {
            font-size: 16px;
            margin-left: 15px;
        }
    }
    &__stars {
        width: 100%;
        display: flex;
    }
}
  
.star {
    cursor: pointer;
    width: 20px;
    height: 20px;
    color: $light;
    margin: 0 2px;
}

.star.selected {
    color: $accent;
}

//EDITABLE TEXT
.user-note {
    margin-top: 30px;
    display: flex;
    width: 100%;
    &__copy {
        width: 100%;
        min-height: 50px;
        line-height: 24px;
    }
    &__copy_active, &__input_active {
        color: #ffffff;
        text-align: inherit;
        padding: 0;
        background: transparent;
        border: none;
        outline: none;  
    }
    &__copy_active {
        cursor: pointer;
    }
    &__input_active {
        overflow: hidden;
        text-align: left;
        width: 100%;
        min-height: 150px;        
        display: flex;
        resize: vertical;
    }
    &__copy_hidden, &__input_hidden {
        display: none;
    }
}

@media screen and (max-width: 799px) {
    .seenlist__items {
        justify-content: center;
    }
    .seenlist-item {
        width: 90%;
    }
}

@media screen and (max-width: 440px) {
    .seenlist-item {
        flex-wrap: wrap;
        &__left, &__right {
            width: 100%;
        }
        &__right {
            margin: 20px 0;
        }
    }
}